import { useContext, useEffect } from 'react'
import { PhaseContext } from '../../Context/PhaseContext'
import { UserDataContext } from '../../Context/UserDataContext'
import s from './PictureButton.module.scss'

export default (props) => {
    const data = useContext(UserDataContext)
    const phase = useContext(PhaseContext)

    useEffect(() => {
        if(data.userData.prevPhase === phase.phase){
            data.setUserData({...data.userData, [props.name]: undefined})
        }
    }, [])

    return(
        <div onClick={() => {
            !data.userData[props.name] && data.setUserData({...data.userData, [props.name]: props.text})
            phase.setPhaseFetch(true)
            setTimeout(() => {
                phase.setPhase(props.nextPhase)
            }, 3000)
        }} className={`${s.answer_picture} ${data.userData[props.name] === undefined 
                                                ? null 
                                                : data.userData[props.name] === props.text 
                                                    ? s.active_choose 
                                                    : s.not_active_choose}`}>
            <div>{props.text}</div>
            <img draggable={false} src={props.image} alt="" />
        </div>
    )
}