import { useContext, useEffect, useRef } from 'react'
import { PhaseContext } from '../../Context/PhaseContext'
import { UserDataContext } from '../../Context/UserDataContext'
import s from './Button.module.scss'

export default (props) => {
    const data = useContext(UserDataContext)
    const phase = useContext(PhaseContext)

    useEffect(() => {
        if(data.userData.prevPhase === phase.phase){
            data.setUserData({...data.userData, [props.name]: undefined})
        }
    }, [])

    return(
        <div className={`${s.answer_item} ${data.userData[props.name] === undefined 
            ? null 
            : data.userData[props.name] === props.text 
                ? s.active_choose 
                : s.not_active_choose}`}>
            <input checked={data.userData[props.name] === props.text} disabled={data.userData[props.name]} type="radio" name={props.name} id={props.text} />
            <label  onClick={e => !data.userData[props.name] && (
                data.setUserData({...data.userData, [props.name]: props.text}),
                phase.setPhaseFetch(true),
                setTimeout(() => {
                    phase.setPhase(props.nextPhase)
                }, 3000)
            )} name={props.name} htmlFor={props.text}>{props.text}</label>
        </div>
    )
}